import React from 'react';
import { Button, Modal, Image } from 'react-bootstrap';

function MyVerticallyCenteredModal(props) {

  var data = props.datos.datos;

  return (
    <Modal
      {...props}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          {data.name}
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <div className="text-center">
          {data.logo_pic ? <Image src={data.logo_pic} height="250px"></Image> : <strong>No hay foto.</strong>}
        </div>     
        <p>
          <b>Contacto:</b><br></br>
          <div className="ml-3">{data.contact ? data.contact : 'No hay información.'}</div>       
        </p>
        <p>
          <b>Teléfono:</b>
          <div className="ml-3">{data.phone ? data.phone : 'No hay información.'}</div>  
        </p>
        <p>
          <b>Descripción:</b>
          <div className="ml-3">{data.description ? data.description: 'No hay descripción.'}</div>  
        </p>
        <p>
          <b>Pagos:</b>
          {(data.payment === 'Al día') ? <div className="ml-3 text-success">Al día.</div> : ''}
          {(data.payment === 'No pagado') ? <div className="ml-3 text-danger">No pagado.</div> : ''}
        </p>
        <p>
          <b>Recursos:</b>
          <div className="ml-3">{data.resources ? <a href={data.resources} target="_blank">Pincha aquí para acceder a los recursos.</a> : 'No hay recursos.'}</div>
        </p>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="secondary" onClick={props.onHide}>Cerrar</Button>
      </Modal.Footer>
    </Modal>
  );
}
  
function Infocmmodal(props) {
  const [modalShow, setModalShow] = React.useState(false);

  return (
    <>
      <Button variant="light" className="btn-sm" onClick={() => setModalShow(true)}>
        <svg width="1em" height="1em" viewBox="0 0 16 16" className="bi bi-eye-fill" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
          <path d="M10.5 8a2.5 2.5 0 1 1-5 0 2.5 2.5 0 0 1 5 0z"/>
          <path fillRule="evenodd" d="M0 8s3-5.5 8-5.5S16 8 16 8s-3 5.5-8 5.5S0 8 0 8zm8 3.5a3.5 3.5 0 1 0 0-7 3.5 3.5 0 0 0 0 7z"/>
        </svg>
      </Button>

      <MyVerticallyCenteredModal
        show={modalShow}
        onHide={() => setModalShow(false)}
        datos={props}
      />
    </>
  );
}

export default Infocmmodal;