import React from 'react';
import {Button, Modal, Form } from 'react-bootstrap';
import {db} from '../../../services/firestore.js';


function MyVerticallyCenteredModal(props) {  

  function createComp(datos_tabla){

    // Datos metidos en el formulario:
    var name = document.querySelector('#formNameCOMP').value;
    var contact = document.querySelector('#formContact').value;
    var logo_pic = document.querySelector('#formLogoPic').value;
    var description = document.querySelector('#formDescriptionCOMP').value;
    var payment = document.querySelector('#formPayment').value;
    var phone = document.querySelector('#formPhone').value;
    var resources = document.querySelector('#formResources').value;

   
    db.collection("companies").doc().set({
      'contact': contact,
      'description': description,
      'name': name,
      'payment': payment,
      'phone': phone,
      'resources': resources,
      'logo_pic': logo_pic  
    })
    .then(function() {
        console.log("Document successfully written!");
        window.location.reload(false);
    })
    .catch(function(error) {
        console.error("Error writing document: ", error);
    });
    
  }

  return (
    <Modal
      {...props}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          Crear Empresa
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Form>
          <Form.Group controlId="formNameCOMP">
            <Form.Label>Nombre de la Empresa:</Form.Label>
            <Form.Control type="text" required placeholder="Escribe aquí el nombre de la Empresa." />
          </Form.Group>
          <Form.Group controlId="formLogoPic">
            <Form.Label>Logo de la Empresa:</Form.Label>
            <Form.Control type="text" required placeholder="Coloca aquí el enlace del logo." />
          </Form.Group>
          <Form.Group controlId="formContact">
            <Form.Label>Contacto</Form.Label>
            <Form.Control type="text" placeholder="Escribe aquí el nombre de el/la contacto de la Empresa." />
          </Form.Group>
          <Form.Group controlId="formPhone">
            <Form.Label>Teléfono</Form.Label>
            <Form.Control type="text" placeholder="Escribe aquí el teléfono del contacto de la Empresa." />
          </Form.Group>
          <Form.Group controlId="formDescriptionCOMP">
            <Form.Label>Descripción:</Form.Label>
            <Form.Control as="textarea" rows={3} placeholder="Describe la información general que tienes sobre la Empresa." />
          </Form.Group>
          <Form.Group controlId="formPayment">
            <Form.Label>Pagos:</Form.Label>
            <Form.Control as="select">
              <option value="Al día">Al día</option>
              <option value="No pagado">No pagado</option>
            </Form.Control>
          </Form.Group>
          <Form.Group controlId="formResources">
            <Form.Label>Recursos:</Form.Label>
            <Form.Control type="text" placeholder="Añade la url de la imagen." />
          </Form.Group>
        </Form>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="dark" onClick={() => createComp(props)} onClickCapture={props.onHide}>Guardar</Button>
        <Button variant="secondary" onClick={props.onHide}>Cerrar</Button>
      </Modal.Footer>
    </Modal>
  );
}
  
function Createcmmodal(datos) {
  const [modalShow, setModalShow] = React.useState(false);

  return (
    <>
      <Button variant="dark" className="btn-sm" onClick={() => setModalShow(true)}>
        <svg width="3em" height="3em" viewBox="0 0 16 16" className="bi bi-plus-circle-fill" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
          <path fillRule="evenodd" d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM8.5 4.5a.5.5 0 0 0-1 0v3h-3a.5.5 0 0 0 0 1h3v3a.5.5 0 0 0 1 0v-3h3a.5.5 0 0 0 0-1h-3v-3z"/>
        </svg>
      </Button>

      <MyVerticallyCenteredModal
        show={modalShow}
        onHide={() => setModalShow(false)}
      />
    </>
  );
}

export default Createcmmodal;