import React from 'react';
import { Button, Modal, Form } from 'react-bootstrap';
import {db} from '../../../services/firestore.js';


function MyVerticallyCenteredModal(props) {
  
  var old = props.datos.datos;

  function Editcmmodal(){
    // Datos metidos en el formulario:
    var name = document.querySelector('#formNameCOMP').value;
    var contact = document.querySelector('#formContact').value;
    var description = document.querySelector('#formDescriptionCOMP').value;
    var payment = document.querySelector('#formPayment').value;
    var phone = document.querySelector('#formPhone').value;
    var resources = document.querySelector('#formResources').value;

    db.collection("companies").where("name", "==", name)
    .get()
    .then(querySnapshot => {
      querySnapshot.forEach(function(doc) {
        
        db.collection("companies").doc(doc.id).set({
          'contact': contact,
          'description': description,
          'name': name,
          'payment': payment,
          'phone': phone,
          'resources': resources 
        })
        .then(function() {
          console.log("Document successfully written!");
          window.location.reload(false);
        })
        .catch(function(error) {
          console.error("Error writing document: ", error);
        });
      })
    })
    .catch(function(error) {
        console.log("Error getting documents: ", error);
    });
  }

  return (
    <Modal
      {...props}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          Editar Mafia / Banda
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
      <Form>
          <Form.Group controlId="formNameCOMP">
            <Form.Label>Nombre de la Empresa:</Form.Label>
            <Form.Control type="text" required defaultValue={old.name} />
          </Form.Group>
          <Form.Group controlId="formLogoPic">
            <Form.Label>Logo de la Empresa:</Form.Label>
            <Form.Control type="text" required defaultValue={old.logo_pic} />
          </Form.Group>
          <Form.Group controlId="formContact">
            <Form.Label>Contacto</Form.Label>
            <Form.Control type="text" defaultValue={old.contact} />
          </Form.Group>
          <Form.Group controlId="formPhone">
            <Form.Label>Teléfono</Form.Label>
            <Form.Control type="text" defaultValue={old.phone} />
          </Form.Group>
          <Form.Group controlId="formDescriptionCOMP">
            <Form.Label>Descripción:</Form.Label>
            <Form.Control as="textarea" rows={3} defaultValue={old.description} />
          </Form.Group>
          <Form.Group controlId="formPayment">
            <Form.Label>Pagos:</Form.Label>
            <Form.Control as="select" defaultValue={old.payment}>
              <option value="Al día">Al día</option>
              <option value="No pagado">No pagado</option>
            </Form.Control>
          </Form.Group>
          <Form.Group controlId="formResources">
            <Form.Label>Recursos:</Form.Label>
            <Form.Control type="text" defaultValue={old.resources} />
          </Form.Group>
        </Form>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="dark" onClick={() => Editcmmodal()} onClickCapture={props.onHide}>Guardar</Button>
        <Button variant="secondary" onClick={props.onHide}>Cerrar</Button>
      </Modal.Footer>
    </Modal>
  );
}
  
function Editcmmodal(datostabla) {
  const [modalShow, setModalShow] = React.useState(false);

  return (
    <>
      <Button variant="light" className="btn-sm" onClick={() => setModalShow(true)}>
        <svg width="1em" height="1em" viewBox="0 0 16 16" className="bi bi-pencil-square" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
          <path d="M15.502 1.94a.5.5 0 0 1 0 .706L14.459 3.69l-2-2L13.502.646a.5.5 0 0 1 .707 0l1.293 1.293zm-1.75 2.456l-2-2L4.939 9.21a.5.5 0 0 0-.121.196l-.805 2.414a.25.25 0 0 0 .316.316l2.414-.805a.5.5 0 0 0 .196-.12l6.813-6.814z"/>
          <path fillRule="evenodd" d="M1 13.5A1.5 1.5 0 0 0 2.5 15h11a1.5 1.5 0 0 0 1.5-1.5v-6a.5.5 0 0 0-1 0v6a.5.5 0 0 1-.5.5h-11a.5.5 0 0 1-.5-.5v-11a.5.5 0 0 1 .5-.5H9a.5.5 0 0 0 0-1H2.5A1.5 1.5 0 0 0 1 2.5v11z"/>
        </svg>
      </Button>

      <MyVerticallyCenteredModal
        datos={datostabla}
        show={modalShow}
        onHide={() => setModalShow(false)}
      />
    </>
  );
}

export default Editcmmodal;